* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen-Sans, Ubuntu, Cantarell, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  display: block;
}

.timer {
  width: 500px;
  color: #fff;
  background-color: #20b2aa;
  border: 2px solid #212121;
  border-radius: 8px;
  justify-content: center;
  gap: 24px;
  margin-top: 24px;
  margin-left: auto;
  margin-right: auto;
  padding-top: 24px;
  padding-bottom: 24px;
  padding-left: 24px;
  font-size: 24px;
  display: flex;
}

.field {
  flex-direction: column-reverse;
  align-items: center;
  display: flex;
}

.time-wrapper button[data-start] {
  color: #fff;
  width: 50px;
  height: 50px;
  background-color: #008b8b;
  border-bottom-width: 0;
  border-right-width: 0;
  border-radius: 25px;
}

.time-wrapper button[data-start]:hover {
  color: #00f;
}

.time-wrapper button[data-start]:active {
  color: #ff0;
  font-weight: 700;
  box-shadow: inset 6px 6px 14px -2px #f7f3f3c4;
}

.time-wrapper {
  justify-content: center;
  gap: 100px;
  display: flex;
}

#datetime-picker {
  color: #fff;
  background-color: #3f4458;
  border-radius: 4px;
  padding: 12px;
}

.cdt-body {
  background-color: #212121d6;
}

.form label > input {
  width: 100px;
}

